@media only screen and (min-width: 400px){ect-img{
    width: 100%;
}
}


@media only screen and (min-width: 700px) {
.cta{
    font-size: 2.5rem;
    padding: 20px 60px;
}
h1 .section-title {
    font-size: 5rem;
}


#projects .project-item{
    padding: 0px 15px;
    /* margin-bottom: 10px; */
    flex-direction: row;
    height: 400px;
    margin: 0;
    width: 100%;
    border-radius: 0;
}

#projects .project-item:nth-child(even){
    flex-direction: row-reverse;
}

#projects .all-projects .project-info{
    height: 100%;
}

#projects .all-projects .project-img{
    height: 100%;
}

#about .about{
    flex-direction: row;
}
#about .col-left{
    width: 600px;
    height: 400px;
    padding-top: 90px;
    padding-left: 60px;
}
#about .col-left .about-img:after{
    border: 5px solid crimson;
}

#contact .contact{
    flex-direction: column;
    padding: 100px 0;
    align-items: center;
    min-width: 20vh;
}
#contact .contact-items{
    width: 100%;
    display: flex;
    /* background: red; */
    flex-direction: row;
    /* flex-basis: 40%; */
    /* flex-basis: 20% */
    padding: 30px;
    /* margin: 10px; */
    /* background: red; */
}
#contact .contact-item .icon{
    max-height: 90px;
    max-width: 90px;
}
#contact .contact-item{
    /* height: 40vh; */
    flex-direction: row;
    background: rgb(91, 214, 188);
    margin-right: 20px;
}
#contact .contact-item .contact-info{
    width: 100%;
    text-align: left;
    padding-left: 20px;
}

}   

@media screen and (min-width:1100px){


#contact .contact{
    text-align: center;
    justify-content: center;
    margin: 0 auto;
}

}